
import Bloglist from './bloglist';
import Post from './post';
import useFetch from './useFetch';



const Home = () => {
    
    const {data, isPending, error} = useFetch('http://localhost:8001/blogs');
   
    return (
    <div className ="home">
     {isPending && <div> site is loading...</div>}
       <div className="blog">
            <div className="blog-content mg-top"> 
               <Post/>
            </div>
            <div className="side-bar mg-top">
                <div className="search">
                   <div className="s-content">
                    <h1>
                            SEARCH
                        </h1>
                        <div className="search-content">
                            <input type="text" className="search-input"placeholder="Search For..."/>
                            <button className="search-button">
                                <svg className="search-icon"fill="none" stroke="white" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path></svg>
                                </button>
                        </div>
                   </div>
                </div>
                <div className="recent-post">
                
                    {error && <div> {error}</div>}
                   
                    {data && <Bloglist blogs={data }  />}
                </div>
                <div className="archive">
                    <div className="archives">
                        <h1>ARCHIVES</h1>
                        <div className="archive-links">
                            <ul>
                            <li><a href="/">January 2022</a></li>
                            <li><a href="/">February 2022 </a></li>
                            <li><a href="/">March 2022</a></li>
                            <li><a href="/">August 2022</a></li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="tags">
                    <div className="tags-content">
                        <h1>TAGS</h1>
                        <div className="tag-links">
                           
                            <a href="/">web</a>
                            <a href="/">blog</a>
                            <a href="/">development</a>
                            <a href="/">programming</a>
                            
                           
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>

    );}
 
export default Home;