import { Link } from "react-router-dom";
//retrieving data property sent from the home page.
const Bloglist = (props) => {

   const blogs=props.blogs;

   // filtering out the first object property.

   const filtered =blogs.filter(obj =>{
    return obj.id !==1;
})

   
    return ( 
        <div className="blog-list">
       
        <h1>RECENT POSTS</h1>
            {filtered.map((blog) =>(
                 <div className="blog-preveiw" key= {blog.id}>
                    <Link to={`/blogs/${blog.id}`}>
                         <h2>TITLE:<span>{blog.title} </span></h2>
                        <img src={blog.pathImage} alt="image"/>
                         <h2>By: <span>{blog.author}</span></h2>
                    </Link>
     
                 </div>

                ))}
            </div>

          );
        }
 
export default Bloglist;