import {Link} from 'react-router-dom';

const Navbar = () => {
    return (  
<nav className="navbar">
    <div className="link">
        <h1>Blog</h1>
        <div className="links">
            <Link to="/">Home</Link>
            <Link to="/">About</Link>
            <Link to="/">Contact</Link>
            <Link to="/create" style= {{
                color:'white',
            }}>New Post</Link>
        </div>
        <div className="details">
            <h1>BLOG DETAILS</h1>
        </div>
    </div>

        
 </nav>

      
    );
}
 
export default Navbar;